@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Roboto:100,300,400,700");
@import url("https://fonts.googleapis.com/css?family=Passion+One:400,900");
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.12.1/css/all.min.css");
/*
// $breakpoint argument choices
//
// - smallest-phone
// - small-phone
// - phone
// - tab-port
// - tab-land
// - big-desktop
//
// 1em = 16px
*/
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain) */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}
@media only screen and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media only screen and (max-width: 56.5em) {
  html {
    font-size: 55%;
  }
}
@media only screen and (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

body {
  box-sizing: border-box;
  padding: 3rem;
  overflow-x: hidden;
}
@media only screen and (max-width: 37.5em) {
  body {
    padding: 0;
  }
}

a {
  text-decoration: none;
  color: #ed1c24;
  position: relative;
  transition: color 0.3s;
}
a:hover {
  color: #41437f;
}

.onhold a {
  text-decoration: none;
  color: #ed1c24;
  position: relative;
  transition: all 0.3s;
}
.onhold a::before {
  content: "";
  background: #ed1c24;
  height: 2px;
  position: absolute;
  width: 100%;
  transition: all 0.3s;
  transform: scale(0);
}
.onhold a::after {
  content: "";
  background: #ed1c24;
  height: 2px;
  width: 100%;
  position: absolute;
  top: 2rem;
  left: 0;
  transition: all 0.3s;
  transform: scale(0);
}
.onhold a:hover::before {
  transform: scale(1);
}
.onhold a:hover::after {
  transform: scale(1);
}

img {
  width: 100%;
  height: auto;
  display: block;
}

::selection {
  background-color: #e6db19;
  color: #ffffff;
}

.contextual-region {
  position: static;
}

.icon, .icon--external-link::before, .icon--user::before, .icon--copy::before {
  font-family: "Font Awesome 5 Free";
  font-style: normal;
  font-variant: normal;
  display: inline-block;
  padding-right: 0.6rem;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  transition: font-size 0.3s;
}
.icon--copy::before {
  content: "";
}
.icon--user::before {
  padding-right: 1.2rem;
  content: "";
}
.icon--external-link::before {
  content: "";
  font-weight: 900;
}
.icon--external-link:hover::before {
  color: #d5343b;
}

.icon-brands, .icon-brands--facebook::before {
  font-family: "Font Awesome 5 Brands";
  font-style: normal;
  font-variant: normal;
  display: inline-block;
  padding-right: 0.6rem;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  transition: font-size 0.3s;
}
.icon-brands--facebook::before {
  content: "";
}

