.row {
  max-width: 114rem;
  margin: 0 auto;
}
@media only screen and (max-width: 56.5em) {
  .row {
    max-width: 95rem;
    padding: 0 3rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .row {
    max-width: 100vw;
    padding: 0 3rem;
  }
}
.row::after {
  content: "";
  display: table;
  clear: both;
}
.row [class^=col-] {
  float: left;
}
.row [class^=col-]:not(:last-child) {
  margin-right: 5rem;
}
@media only screen and (max-width: 56.5em) {
  .row [class^=col-]:not(:last-child) {
    margin-right: 5rem;
    margin-bottom: 6rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .row [class^=col-]:not(:last-child) {
    margin-right: 0;
    margin-bottom: 6rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .row [class^=col-] {
    width: 100% !important;
  }
}
.row .col-1-of-2 {
  width: calc((100% - 5rem) / 2);
}
.row .col-1-of-3 {
  width: calc((100% - 2 * 5rem) / 3);
}
.row .col-2-of-3 {
  width: calc(2 * ((100% - 2 * 5rem) / 3) + 5rem);
}
.row .col-1-of-4 {
  width: calc((100% - 3 * 5rem) / 4);
}
.row .col-2-of-4 {
  width: calc(2 * ((100% - 3 * 5rem) / 4) + 5rem);
}
.row .col-3-of-4 {
  width: calc(3 * ((100% - 3 * 5rem) / 4) + (2 * 5rem));
}

