.navigation__checkbox {
  display: none;
}
.navigation__button {
  background-color: #ffffff;
  height: 7rem;
  width: 7rem;
  position: fixed;
  top: 6rem;
  right: 6rem;
  z-index: 190;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.1);
  text-align: center;
  cursor: pointer;
}
@media only screen and (max-width: 56.5em) {
  .navigation__button {
    top: 4rem;
    right: 4rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .navigation__button {
    top: 3rem;
    right: 3rem;
  }
}
@media only screen and (max-width: 30em) {
  .navigation__button {
    height: 5rem;
    width: 5rem;
  }
}
.navigation__background {
  height: 6rem;
  width: 6rem;
  position: fixed;
  top: 6.5rem;
  right: 6.5rem;
  background-image: radial-gradient(#2e3192, #41437f);
  z-index: 170;
  transition: transform 0.8s cubic-bezier(0.86, 0, 0.07, 1);
}
@media only screen and (max-width: 56.5em) {
  .navigation__background {
    top: 4.5rem;
    right: 4.5rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .navigation__background {
    top: 3.5rem;
    right: 3.5rem;
  }
}
@media only screen and (max-width: 30em) {
  .navigation__background {
    height: 4rem;
    width: 4rem;
  }
}
.navigation__nav {
  height: 100vh;
  position: fixed;
  top: 0;
  right: -100%;
  z-index: 175;
  opacity: 0;
  width: 0;
  transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 20vh 60vh 20vh;
  grid-template-areas: "title title title" ". nav ." "privacy . .";
}
@media only screen and (max-width: 37.5em) {
  .navigation__nav {
    grid-template-columns: 1fr;
    grid-template-areas: "title" "nav" "privacy";
  }
}
.navigation__title {
  grid-area: title;
  font-size: 10rem;
  text-transform: uppercase;
  color: #ffffff;
  padding: 3rem;
}
@media only screen and (max-width: 37.5em) {
  .navigation__title {
    font-size: 5rem;
  }
}
.navigation__privacy {
  grid-area: privacy;
  transform: rotate(-90deg);
  align-self: center;
  justify-self: left;
  padding: 0 3rem;
}
.navigation__privacy__link {
  color: rgba(255, 255, 255, 0.5);
  position: relative;
  text-transform: uppercase;
}
.navigation__privacy__link::after {
  content: "";
  position: absolute;
  border: dashed 1px;
  top: 3.5rem;
  left: -3rem;
  width: 12rem;
}
@media only screen and (max-width: 37.5em) {
  .navigation__privacy__link::after {
    top: -1.5rem;
  }
}
.navigation__privacy__link:hover {
  color: #d5343b;
}
@media only screen and (max-width: 37.5em) {
  .navigation__privacy {
    transform: rotate(0deg);
    align-self: end;
    justify-self: center;
  }
}
.navigation__items {
  grid-area: nav;
  align-self: center;
  justify-self: center;
}
.navigation__list {
  list-style: none;
  text-align: center;
}
@media only screen and (max-width: 37.5em) {
  .navigation__list {
    width: 80vw;
  }
}
.navigation__item {
  margin: 1rem;
}
.navigation__link:link, .navigation__link:visited {
  display: inline-block;
  font-size: 3rem;
  font-weight: 100;
  color: #ffffff;
  padding: 1rem 5rem;
  text-decoration: none;
  text-transform: uppercase;
  background-image: linear-gradient(120deg, transparent 0%, transparent 50%, #ffffff 50%);
  background-size: 220%;
  transition: all 0.3s;
}
.navigation__link:link span, .navigation__link:visited span {
  margin-right: 1.5rem;
  display: inline-block;
}
@media only screen and (max-width: 37.5em) {
  .navigation__link:link, .navigation__link:visited {
    padding: 0.5rem 5rem;
  }
}
@media only screen and (max-width: 20em) {
  .navigation__link:link, .navigation__link:visited {
    font-size: 2rem;
  }
}
.navigation__link:hover, .navigation__link:active {
  background-position: 100%;
  color: #292e1e;
  transform: translateX(1rem);
}
.navigation__checkbox:checked ~ .navigation__background {
  transform: scale(150);
}
.navigation__checkbox:checked ~ .navigation__nav {
  opacity: 1;
  width: 100%;
  right: 0;
}
.navigation__icon {
  position: relative;
  transform: translateY(3.5rem);
}
@media only screen and (max-width: 30em) {
  .navigation__icon {
    transform: translateY(2.5rem);
  }
}
.navigation__icon, .navigation__icon::before, .navigation__icon::after {
  width: 3rem;
  height: 2px;
  background-color: #292e1e;
  display: inline-block;
  transition: all 0.3s;
}
.navigation__icon::before, .navigation__icon::after {
  content: "";
  position: absolute;
  left: 0;
}
.navigation__icon::before {
  top: -0.8rem;
}
.navigation__icon::after {
  top: 0.8rem;
}
.navigation__button:hover .navigation__icon::before {
  top: -1rem;
}
.navigation__button:hover .navigation__icon::after {
  top: 1rem;
}
.navigation__checkbox:checked + .navigation__button .navigation__icon {
  background-color: transparent;
}
.navigation__checkbox:checked + .navigation__button .navigation__icon::before {
  transform: rotate(135deg);
  top: 0;
}
.navigation__checkbox:checked + .navigation__button .navigation__icon::after {
  transform: rotate(-135deg);
  top: 0;
}

